import React, { Fragment } from "react";
import { connect } from "react-redux";
import {
  SIO_RECEIVE_DRAWN_TILE,
  SIO_UPDATE_GAME_ID,
  SIO_UPDATE_GAME_STATE,
} from "./redux/actions";

const SocketContainer = (props) => {
  const { socket, receiveDrawnTile, updateGameID, updateGameState } = props;

  socket.on("join-game", (gameID) => {
    updateGameID(gameID);
  });

  socket.on("send-tiles", (tiles) => {
    receiveDrawnTile(tiles);
  });

  socket.on("update-client-from-server", (gameState) => {
    console.log("update-client-from-server called");
    updateGameState(gameState);
  });

  return <Fragment>{props.children}</Fragment>;
};

const mapStateToProps = (state, ownProps) => {
  return {};
};
const mapDispatchToProps = {
  updateGameID: (gameID) => ({ type: SIO_UPDATE_GAME_ID, payload: gameID }),
  updateGameState: (gameState) => ({
    type: SIO_UPDATE_GAME_STATE,
    payload: gameState,
  }),
  receiveDrawnTile: (tiles) => ({
    type: SIO_RECEIVE_DRAWN_TILE,
    payload: tiles,
  }),
};

export default connect(mapStateToProps, mapDispatchToProps)(SocketContainer);
